 
 <template>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                Name- {{ itemData.EPC_TAG}}
                            </h5>
                            <button v-on:click="CloseItems" href="javascript:;" class="close"><i class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div class="d-flex flex-wrap ml-4">
                            <div class="col-md-12 p-0 mt-4">
                                <div class="heading-border">
                                    <h5 class="h5"><span>Pair Unique Items</span></h5>
                                </div>
                            </div>
                        </div>
                        <div class="border p-3">
                            <dynamic-form lang="en" ref="GetItem" :buttons="buttons" :schema="FormSchema"  v-on:OnSubmit="onSubmit"></dynamic-form>
                            <span class="mandatory"> {{ $t('Required') }}</span>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </template>



<script>
import DataService from '../../service/DataService';
export default {   
    props:{
        itemData:Array,        
    },
    data() {
        return {
            isLoading:false,
            CompanyId:'',
            UserId:'',
            UniqueData:[],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                            {
                                astype: "SelectField",
                                label: this.$t('ItemName'),
                                name: "ItemName",
                                value: "",
                                placeholder: "",
                                //disabled: true,
                                validationRules: { "required": true, },
                                config: {
                                    options: [],
                                    onChange: this.onChangeDropdown
                                },
                            },
                            {
                                astype: "SelectField",
                                label: this.$t('Name'),
                                name: "Name",
                                value: "",
                                placeholder: "",
                                //disabled: true,
                                validationRules: { "required": true },
                                config: {
                                    options: [],
                                    //onChange: this.onChangeDropdown
                                },

                            },                        
                      ]
                },             
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseItems
                }
            ],
        }
    },
    created: function () {
        //Debugger;
        var vm=this;       
        vm.UserId=vm.GetUserInfo.ID,
        vm.CompanyId= vm.GetUserInfo.CompanyId,        
        this.FetchData();
        // this.GetScannedList();
        
    },
    methods: {
        CloseItems: function () {
            var vm = this;
            this.$emit('CloseGetItems')
        },
        FetchData(){
            //Debugger;
            var vm = this;
            var url =`?UserId=${vm.UserId}&companyId=${vm.CompanyId}`;
             DataService.GetItems(url).then(response => {
                //Debugger;                                  
                    if (response.data.length > 0) {
                        //Debugger;
                        response.data.forEach((item, index) => {
                             (vm.FormSchema)[0].Data[0].config.options.push({
                                 name: item.NAME,
                                 value: `${item.ASSET_CATALOGUE_ID}`  
                             })                            
                        }); 
                        vm.UpdateKeyVale()                        
                    }                
            });
        },
         onChangeDropdown(evt) {
            //Debugger
            var vm = this; 
            vm.isLoading=true;           
            var url =`?UserId=${vm.UserId}&companyId=${vm.CompanyId}&id=${evt.target.value}`;          
             DataService.GetUniqueNamesForRFID(url).then(function (response) {
                //Debugger;                                             
                if (response.data.length > 0) {
                    //Debugger;
                    (vm.FormSchema)[0].Data[1].config.options=[];                   
                        response.data.forEach((item, index) => {
                             (vm.FormSchema)[0].Data[1].config.options.push({
                                 name: item.UNIQUE_NAME,
                                 value: `${item.RELATED_INFO_ID}`  
                             })                            
                        });                       
                    vm.UpdateKeyVale();
                    vm.isLoading=false; 
                }                                             
            });
        },
        UpdateKeyVale(){
            debugger;
            this.$refs.GetItem.UpdateKeyValue(); 
        },        
        async onSubmit(formfields) {
                let vm = this;
                const obj = {
                    companyId : vm.CompanyId,
                    loggedinUserId : vm.UserId,
                    id :vm.itemData.Id,                     
                    itemUniqueID : formfields.Name                                      
                }
                //Debugger;
                let jsonObject = JSON.stringify(obj);
                await DataService.UpdateItemsForRFIDTag(obj).then((response) => {
                    response.data.Status               
                   
                    if (response.data != null) {
                        if (response.data.Status == "-1") {                         
                            vm.ShowAlert( response.data.AssignedToName,"Failure",vm.$t("Alert"));
                            vm.$parent.GetScannedList();
                        }
                        else if (response.data.Status == "2") { 
                            vm.ShowAlert( response.data.AssignedToName,"Warning",vm.$t("Alert")); 
                            vm.$parent.GetScannedList();                          
                            //vm.ShowAlert("This item is already assigned to " + data.asignedto + ", please wait for return.");
                        }
                        else {                           
                            vm.ShowAlert("Item attached successfully.", "success", true, vm.$t("Alert"));
                            vm.$parent.GetScannedList();
                            vm.CloseItems();
                        }                       
                    }
                });
        },
        
    }
}
</script>