<template>
    
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('Assign Group') }}
                        </h5>
                        <button v-on:click="CloseAssigngroup" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>
                    </div>
                    <div class="d-flex flex-wrap ml-4">
                        <div class="col-md-12 p-0 mt-4">
                            <div class="heading-border">
                                <h5 class="h5"><span>Assign Group To RFID TAG</span></h5>
                            </div>
                        </div>
                    </div>
                    <div class="modal-body">
                        <div class="row ml-2">
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                                <div class="form-group">
                                    <label>Group Name:</label><span class="text-danger">*</span>

                                    <select name="GroupName" v-model="TxtGroupName"
                                        v-bind:class="{ 'form-control w-100': true }">
                                        <option value="">Select</option>
                                        <option v-for="(item, itemIndex) in GroupData" :key="itemIndex"
                                            :value="item.group_id">{{ item.group_name }}</option>
                                            
                                    </select>


                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <div class="col-lg-12">
                            <small class="text-danger">Fields marked with an asterisk (*) are mandatory.</small>
                        </div>
                        <button class="btn btn-success" @click="Savegroup()"> Save</button>
                        
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../service/DataService';
export default {    
    props:{
        assDetailIDs:Number,
        checkboxarray:Array
    },
    data() {
        return {
            IsShowAddNamePopup: false,
            GroupData: [],
            TxtGroupName: '',
            group_name:'',
            selectedRFIDS:'',
            is_unassigned:'0'


        }
    },
    created: function () { 
        //debugger;       
        this.GetGroupName();
        // this.GetScannedList();
        
    },
    methods: {
        CloseAssigngroup: function () {           
            this.$emit('CloseScannedItem')
        },
        GetGroupName: async function () {            
        var vueObj = this;
          await DataService.GetScannedGroupName().then(response => {
                if (response.data != null) {                    
                    if (response.data.DATA.length > 0) {
                        vueObj.GroupData = response.data.DATA;
                        // this.$emit("GetScannedList");

                    }

                }

            });
        },
        
        Savegroup: async function () {
            //debugger;      
            var vm = this;            
           var obj=  `groupNames=${vm.TxtGroupName}&selectedRFIDS=${vm.assDetailIDs}&is_unassigned=${vm.is_unassigned}`;
            await  DataService.AddSaveAssignGroup(obj).then(response => {              
                if (response.data == 'True') {
                    vm.ShowAlert("Group Assgin successfully.", "success", true, vm.$t("Alert"));
                }
                this.$emit('CloseScannedItem')
                this.$emit('checkboxarray','')               
                this.$parent.GetScannedList();            
               
            });  
           
        },

    }
}
</script>